body {
    background-color: whitesmoke;
}

.container-blogs {
    /* border: 4px solid black; */
    /* display: block; */
    font-family: 'Roboto', sans-serif;
    margin: 50px auto 0;
    max-width: 820px;
    padding: 10px;
    background-color: white;
}

.title-top {
    font-size: 60px;
    text-align: center;
    font-weight: 5000;
    font-family: 'Merriweather', serif;
}

.alert-text {
    font-family: 'Merriweather', serif;
    color: red;
    font-weight: 1000;
}

.yt-video {
    display: block;
    padding: 5px;
    margin-left: auto;
    margin-right: auto;
}

.body {
    font-size: 20px;
}

.ul-heading {
    font-size: 40px;
    text-align: center;
    color: blue;
}

.li-heading {
    font-size: 26px;
}

.container-blogs ul {
    margin: 2px;
}

.container-blogs ol {
    margin: 2px;
}

.container-blogs ul li a {
    /* color: black; */
    color: rgb(207, 56, 56);
    text-decoration: none;
}

.container-blogs ol li a {
    /* color: black; */
    /* color: rgb(207, 56, 56); */
    text-decoration: none;
}

.li-image {
    width: 100%;
    height: 250px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.a-link {
    color: black;
    text-decoration: none;
}

.a-link:hover {
    color: black;
}